import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { TypeOfExecution } from 'app/core/models/type-of-execution.model';
import { TypeOfMediumExecution } from 'app/core/models/type-of-medium-execution.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-type-of-medium-execution',
  templateUrl: './type-of-medium-execution.component.html',
  styleUrls: ['./type-of-medium-execution.component.scss']
})
export class TypeOfMediumExecutionComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<TypeOfExecution>;
  displayedColumns: string[] = ['typeOfExecution', 'applyForTOA', 'applyForS2', 'status'];
  statusList = [...STATUSES];

  typeOfExecution = new TypeOfExecution();
  typeOfExecutionList = [];

  typeOfExecutionForm: FormGroup;

  checked: boolean; // declared in HTML but not in component (feb 14 aot)

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.typeOfExecutions).subscribe((res: any) => {
      this.typeOfExecutionList = res.responseData.data;
      this.dataSource = new MatTableDataSource<TypeOfExecution>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void{
    this.apiService.save(ENDPOINTS.typeOfExecutions, this.typeOfExecution).subscribe(
        (res: any) => {
          if (res !== undefined){
            this.sweetAlertService.success(res);
          }
        }, (err: any) => {
          this.sweetAlertService.error(err);
        }, () => {
          this.resetFormValidator(),
          this.getList();
        }
    )
  }

  getTypeOfExecution(): void {
    this.apiService.findById(ENDPOINTS.typeOfExecutions, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.typeOfExecution = res.responseData.data;
        console.log(res.responseData.data);

      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.typeOfExecutions, this.typeOfExecution).subscribe(
      (res: any) => {
        if (res !== undefined){
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    this.typeOfExecution.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfExecutionForm = this.formBuilder.group({
      typeOfExecution: ['', Validators.required],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.typeOfExecution = new TypeOfExecution();
    this.typeOfExecution.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfExecutionForm.markAsUntouched();
    this.typeOfExecutionForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this type of execution?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.typeOfExecutions, this.typeOfExecution.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Type of execution successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
