import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TypeOfMediumExecutionComponent } from './type-of-medium-execution.component';

const routes: Routes = [
  {
    path: '',
    component: TypeOfMediumExecutionComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TypeOfMediumExecutionRoutingModule { }
